import { useState, useEffect } from 'react'
import Header from './Header'
import Tasks from './Tasks'
import AddTask from './AddTask'
import Button from'./Button'
import { Container } from 'react-bootstrap'
import { Link, useHistory } from 'react-router-dom';
import { auth, firestore_db } from '../firebase'
import { useAuth } from '../contexts/AuthContext'


 
 
const Dashboard = () => {
  const [error, seterror] = useState('')
  const{currentUser, logout} = useAuth()
  const [showAddTask, setShowAddTask] = useState(false)
  const [tasks, setTasks] = useState([])
  const history = useHistory()

  useEffect(() => {
    const getTasks = async () => {
      const tasksFromServer = await fetchTasks()
      setTasks(tasksFromServer)
    }

    getTasks()
  }, [])

  // Fetch Tasks
  const fetchTasks = async () => {
    let currentUser = auth.currentUser
    let user = await firestore_db.collection('users').doc(currentUser.uid).get(); 
    let userData = user.data()
    let userTasks = userData.tasks

    const data = userTasks

    return data
  }

  // Fetch Task
  // const fetchTask = async (id) => {
  //   const res = await fetch(`http://localhost:5000/tasks/${id}`)
  //   const data = await res.json()

  //   return data
  // }

  // Add Task
  const addTask = async (task) => {

    let currentUser = auth.currentUser
    let user = await firestore_db.collection('users').doc(currentUser.uid).get(); 
    let userData = user.data()
    let userTasks = userData.tasks
    userTasks.push(task)

    firestore_db.collection('users').doc(currentUser.uid).update({tasks: userTasks});
    const data = await fetchTasks()

    setTasks([...tasks, task])
    

     
  }

  // Delete Task
  const deleteTask = async (id) => {

  
    let currentUser = auth.currentUser
    let user = await firestore_db.collection('users').doc(currentUser.uid).get(); 
    let userData = user.data()
    let userTasks = userData.tasks
    userTasks.splice(id,1)
 
    
    await firestore_db.collection('users').doc(currentUser.uid).update({tasks: userTasks});

    window.location.reload() 
  }





  

  // Toggle Reminder
  const toggleReminder = async (id) => {
    let currentUser = auth.currentUser
    let user = await firestore_db.collection('users').doc(currentUser.uid).get(); 
    let userData = user.data()
    let userTasks = userData.tasks
    // const taskToToggle = await fetchTask(id)
    const updTask = { ...user, reminder: !user.reminder }


    await firestore_db.collection('users').doc(currentUser.uid).update({tasks: userTasks});

    setTasks(
      tasks.map((task) =>
        task.id === id ? { ...task, reminder: task.reminder } : task
      )
    )
  }

  async function handleLogout() {
    seterror('')

    try {
      await logout()
      history.push('/')
    } catch {
      seterror('Failed to log out')
    }

  }
  


  return ( 
    //<div className="container">
    
    <Container
    
    
      style ={{minHeight: "100vh"}}>
        <div className="w-100" style= {{maxWidth: '400px'}}>
      

        </div>
      
      <Header 
        onAdd = {() => setShowAddTask
        (!showAddTask)} 
        showAdd ={showAddTask} 
      />

      {showAddTask && <AddTask onAdd ={addTask} />}
      {tasks.length > 0 ? <Tasks tasks ={tasks} onDelete =
      {deleteTask} onToggle = {toggleReminder} /> : 'No Tasks To Show'}
      &nbsp;&nbsp;&nbsp;&nbsp;
      &nbsp;&nbsp;&nbsp;&nbsp;
      <div className = "w-100 text-center mt-2">
      <h7 >Calm down those nerves and plan more efficiently, visit the <Link to= "/discover"  style ={{color: 'blue'}}> Discover</Link> page!</h7>
      <div className = "w-100 text-center mt-2">
           <Link to= "/" style ={{color: 'blue'}} onClick={handleLogout} > Log Out</Link>
      </div>
      </div>
       
    </Container>


    
  );
}


export default Dashboard;
